* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.app-wrapper {
    width: 100%;
    margin: 0 auto;
    background-color: black;
    display: grid;
    grid-template-rows: 60px 1fr;
    grid-template-columns: 1fr 11fr;
    grid-template-areas:
  "h h"
  "n c"
}

.app-wrapper-content {
    grid-area: c;
    margin : 10px 10px 10px 0;
    background: linear-gradient(0.5turn, #a63f6a, #f8e1e1, #f63c3c);

}

#root {
    display: flex;
    flex-direction: column;
    align-items: center;
}







